.menu-header {
    padding-bottom: 10px;
}
.menu-header h1 {
    color: white;
    text-align: center;
    font-size: 53px;
    line-height: 70px;
    margin-top: 102px;
    letter-spacing: 2px;
}
.menu-header hr {
    color: orange;
    width: 10%;
    border-color: orange;
    padding: 0px;
    border: 2px solid;
}
.menu-header p {
    color: #d8d0d0;
    text-align: center;
    width: 27%;
    padding: 10px;
    font-size: 16px;
    display: block;
    margin: 0 auto;
    line-height: 29px;
}

.nav-menu {
    margin: 0 auto;
    display: block;
    text-align: center;
}
.nav-menu ul {
    list-style: none;
    padding-bottom: 68px;
}
.nav-menu ul a {
    color: #fff;
    background: #222224;
    padding: 15px 48px;
    margin: 5px;
    border-radius: 3px;
    font-size: 15px;
    letter-spacing: 3px;
    text-decoration: none;
}

.nav-menu ul a:hover {
    color: #000;
    background: #FFBA00;
 
}
.nav-menu ul li:first-child{
    color: #000;
    background: #FFBA00;
 
}