.menu-style{}
.menu-style img {
    display: block;
    height: 110px;
    border-radius: 19px;
    /* margin: 0px auto; */
    width: 29%;
    padding: 50px 93px;
    background-color: #181818;
}
.menu-style h1 {
    color: #fff;
    text-align: left;
    font-size: 23px;
    letter-spacing: 1px;
    line-height: 37px;
}
.menu-style p {
    color: #908e8e;
    width: 77%;
    text-align: left;
    line-height: 23px;
    font-size: 16px;
}
.menu-style h4 {
    font-size: 25px;
    color: #ffb000;
}